.button {
    padding: 15px 55px;
    font-size: 24px;
    font-weight: 600;
    border-radius: 60px;
    width: max-content;
    cursor: pointer;
}

.primary-btn {
    color: var(--white);
    background: var(--btn--bg);
    text-transform: uppercase;
}

.secondary-btn {
    border : 2px solid var(--white);
}