.topfold {
    height: 112vh;
    overflow: hidden;
}

.tf-left {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding-right: 30px;
}
.tf-heading {
    font-size: 62px;
    font-weight: 700;
    line-height: 62px;
}

.tf-description {
    font-size: 18px;
    margin: 18px 0;
}

.tf-left-btns {
    margin-top: 24px;
    display: flex;
}

.tf-left-secondary-btn {
    margin-left : 26px;
}

.tf-left-infoStats {
    display: flex;
    padding: 30px 0px 0px;
}

.tf-is-infoItems{
    margin-right: 18px;
    flex-direction: column;
}

.tf-infoItem-count {
    font-size: 32px;
    font-weight: 600;
}

.tf-infoItem-label{
    font-size: 12px;
    font-weight: 600;
}

.tf-right {
    display: flex;
    flex: 1;
    position: relative;
}

.tf-right-diamond {
    display: flex;
    flex-wrap: wrap;
    transform: rotate(45deg);
}

.tf-r-diamond-item {
    height: 230px;
    width: 220px;
    overflow: hidden;
    margin: 4px;
    transform: rotate(-45deg);
    cursor: pointer;
    border: 3px solid var(--white);
    border-radius: 12px;
}

.tf-r-diamond-item-img {
    height: 300px;
    object-fit: contain;
    display: block;
}

.tf-r-bg-blob{
    height: 500px;
    width: 500px;
    background-color: var(--bg-blob);
    filter: blur(160.58px);
    position: absolute;
    z-index: -99;
    left: 20%;
    top: 10%;
    transform: translate(-50%);
}